import React, { useState } from 'react';
import Picker, { IEmojiData } from 'emoji-picker-react';
import { Divider, Button, Card, CardHeader, CardActions, Backdrop, TextField, InputAdornment } from '@mui/material';
import { makeStyles } from '@mui/styles';

const useStyles = makeStyles({
    root: {
        borderRadius: 12,
        minWidth: 175,
        textAlign: 'center',
    },
    header: {
        textAlign: 'center',
        spacing: 10,
    },
    button: {
        margin: 2,
        fontSize: 32,
    },
    action: {
        display: 'flex',
        justifyContent: 'space-around',
    },
});

interface EmojiDropdownProps {
    /**
     * The label for the dropdown
     */
    label: string;
    /**
     * What emoji to use as the initially selected emoji
     */
    defaultEmoji: string;
    /**
     * What emoji name to use as the initially selected slack emoji
     */
    defaultSlackEmoji: string;
    /**
     * Whether or not to accept custom emoji name
     */
    slackMode: boolean;
    /**
     * Optional change handler
     */
    onChange: (emoji: string) => void;
}

/**
 * Primary UI component for user interaction
 */
export const EmojiDropdown = ({
    label,
    defaultEmoji,
    defaultSlackEmoji,
    slackMode,
    onChange,
}: EmojiDropdownProps) => {
    const classes = useStyles();

    const [selectedEmoji, setSelectedEmoji] = useState(defaultEmoji);
    const [selectedSlackEmoji, setSelectedSlackEmoji] = useState(defaultSlackEmoji);
    const [picking, setPicking] = useState(false);

    const onEmojiClick = (event: React.MouseEvent<Element, MouseEvent>, data: IEmojiData) => {
        setSelectedEmoji(data.emoji);
        setPicking(false);
        onChange(data.emoji);
    };

    const onSlackChange = (event: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => {
        setSelectedSlackEmoji(event.target.value);
        onChange(event.target.value);
    };

    const showPicker = () => setPicking(true);
    const closePicker = () => setPicking(false);

    return (
        <Card className={classes.root}>
            <CardHeader title={label} className={classes.header} />
            <Divider variant="middle" />
            <CardActions className={classes.action}>
                {slackMode ?
                    <TextField
                        label={label}
                        defaultValue={selectedSlackEmoji}
                        onChange={onSlackChange}
                        InputProps={{
                            startAdornment: <InputAdornment position="start">:</InputAdornment>,
                            endAdornment: <InputAdornment position="end">:</InputAdornment>,
                        }}
                    /> : <>
                        <Button variant="outlined" className={classes.button} onClick={showPicker}>
                            {selectedEmoji}
                        </Button>
                        <Backdrop
                            sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
                            open={picking}
                            onClick={closePicker}>
                            <div onClick={e => e.stopPropagation()}>
                                <Picker native preload onEmojiClick={onEmojiClick} />
                            </div>
                        </Backdrop>
                    </>
                }
            </CardActions>
        </Card>
    );
};