import { Share } from '@mui/icons-material';
import { Alert, Button, Card, CardActions, CardHeader, Collapse, Divider, Typography } from '@mui/material';
import { makeStyles } from '@mui/styles';
import React, { useState } from 'react';

interface WordleOutputProps {
    /**
     * What wordle to start with
     */
    wordle: string;
}

const useStyles = makeStyles({
    root: {
        borderRadius: 12,
        minWidth: 175,
        textAlign: 'center',
    },
    header: {
        textAlign: 'center',
        spacing: 10,
    },
    list: {
        padding: '20px',
        whiteSpace: 'pre-wrap'
    },
    button: {
        margin: 2,
    },
    action: {
        display: 'flex',
        justifyContent: 'space-around',
    },
});

export const WordleOutput = ({
    wordle,
}: WordleOutputProps) => {
    const classes = useStyles();

    const [showToast, setShowToast] = useState(false);

    const copyToClipboard = () => {
        navigator.clipboard.writeText(wordle);
        setShowToast(true);
        window.setTimeout(() => {
            setShowToast(false);
        }, 5000);
    };

    return (
        <>
            <Card className={classes.root}>
                <CardHeader title="Converted Wordle" className={classes.header} />
                <Divider variant="middle" />
                <div className={classes.list}>
                    <Typography align="center">{wordle}</Typography>
                </div>
                <Divider variant="middle" />
                <CardActions className={classes.action}>
                    <Button variant='contained' onClick={copyToClipboard} startIcon={<Share />}>
                        Share
                    </Button>
                </CardActions>
                <Collapse in={showToast}>
                    <Alert severity="info">Copied converted Worder to clipboard!</Alert>
                </Collapse>
            </Card>
        </>
    );
};