import React from 'react';
import { TextField } from '@mui/material';

interface WordleInputProps {
    /**
     * What wordle to start with
     */
    defaultWordle: string;
    /**
     * Change handler
     */
    onChange: (wordle: string) => void;
}

/**
 * Primary UI component for user interaction
 */
export const WordleInput = ({
    defaultWordle,
    onChange,
}: WordleInputProps) => {
    const onChangeWordle = (event: any) => onChange(event.target.value);

    return (
        <TextField
            margin='normal'
            fullWidth={false}
            tabIndex={0}
            label="Enter your Wordle results"
            multiline
            placeholder={defaultWordle}
            onChange={onChangeWordle}
        />
    );
};